'use client';

import { useState, useEffect, useCallback } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import {
  fetchPool,
  fetchPoolLimits,
  fetchPoolStats,
  depositToPool,
  withdrawFromPool,
  setPoolPhase,
  formatCurrency,
  formatPercent,
  getPhaseColor,
  getPhaseBgColor,
  getPhaseLabel,
  getPhaseDescription,
  type PoolData,
  type PoolLimits,
  type PoolStats,
} from '@/lib/pool-api';

interface Agent {
  id: string;
  name: string;
  email: string;
  spinCredits: number;
}

export default function PoolPage() {
  const router = useRouter();
  const [agent, setAgent] = useState<Agent | null>(null);
  const [pool, setPool] = useState<PoolData | null>(null);
  const [limits, setLimits] = useState<PoolLimits | null>(null);
  const [stats, setStats] = useState<PoolStats | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [statsPeriod, setStatsPeriod] = useState<'24h' | '7d' | '30d' | 'all'>('24h');

  // Modal states
  const [showDepositModal, setShowDepositModal] = useState(false);
  const [showWithdrawModal, setShowWithdrawModal] = useState(false);
  const [showPhaseModal, setShowPhaseModal] = useState(false);
  const [modalAmount, setModalAmount] = useState('');
  const [modalDescription, setModalDescription] = useState('');
  const [modalLoading, setModalLoading] = useState(false);
  const [modalError, setModalError] = useState('');
  const [modalSuccess, setModalSuccess] = useState('');

  const loadData = useCallback(async () => {
    try {
      const [poolRes, limitsRes, statsRes] = await Promise.all([
        fetchPool(),
        fetchPoolLimits(),
        fetchPoolStats(statsPeriod),
      ]);

      if (poolRes.success && poolRes.data) {
        setPool(poolRes.data);
      }
      if (limitsRes.success && limitsRes.data) {
        setLimits(limitsRes.data);
      }
      if (statsRes.success && statsRes.data) {
        setStats(statsRes.data);
      }
    } catch (err) {
      setError('Erro ao carregar dados do pool');
      console.error(err);
    } finally {
      setLoading(false);
    }
  }, [statsPeriod]);

  useEffect(() => {
    const token = localStorage.getItem('agentToken');
    const agentData = localStorage.getItem('agentData');

    if (!token || !agentData) {
      router.push('/');
      return;
    }

    setAgent(JSON.parse(agentData));
    loadData();
  }, [router, loadData]);

  // Atualizar stats quando per√≠odo mudar
  useEffect(() => {
    if (agent) {
      fetchPoolStats(statsPeriod).then((res) => {
        if (res.success && res.data) {
          setStats(res.data);
        }
      });
    }
  }, [statsPeriod, agent]);

  async function handleDeposit() {
    setModalError('');
    setModalSuccess('');
    const amount = parseFloat(modalAmount);
    
    if (isNaN(amount) || amount <= 0) {
      setModalError('Valor inv√°lido');
      return;
    }

    setModalLoading(true);
    const result = await depositToPool(amount, modalDescription || undefined);
    setModalLoading(false);

    if (result.success) {
      setModalSuccess(`Dep√≥sito de ${formatCurrency(amount)} realizado com sucesso!`);
      setModalAmount('');
      setModalDescription('');
      loadData();
      setTimeout(() => {
        setShowDepositModal(false);
        setModalSuccess('');
      }, 1500);
    } else {
      setModalError(result.message || 'Erro ao depositar');
    }
  }

  async function handleWithdraw() {
    setModalError('');
    setModalSuccess('');
    const amount = parseFloat(modalAmount);
    
    if (isNaN(amount) || amount <= 0) {
      setModalError('Valor inv√°lido');
      return;
    }

    if (pool && amount > pool.balance) {
      setModalError('Saldo insuficiente no pool');
      return;
    }

    setModalLoading(true);
    const result = await withdrawFromPool(amount, modalDescription || undefined);
    setModalLoading(false);

    if (result.success) {
      setModalSuccess(`Saque de ${formatCurrency(amount)} realizado com sucesso!`);
      setModalAmount('');
      setModalDescription('');
      loadData();
      setTimeout(() => {
        setShowWithdrawModal(false);
        setModalSuccess('');
      }, 1500);
    } else {
      setModalError(result.message || 'Erro ao sacar');
    }
  }

  async function handleSetPhase(phase: 'retention' | 'normal' | 'release') {
    setModalLoading(true);
    const result = await setPoolPhase(phase);
    setModalLoading(false);

    if (result.success) {
      loadData();
      setShowPhaseModal(false);
    } else {
      setModalError(result.message || 'Erro ao definir fase');
    }
  }

  function handleLogout() {
    localStorage.removeItem('agentToken');
    localStorage.removeItem('agentData');
    router.push('/');
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-linear-to-br from-slate-900 via-slate-800 to-slate-900 flex items-center justify-center">
        <div className="h-8 w-8 animate-spin rounded-full border-4 border-emerald-500 border-t-transparent" />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-linear-to-br from-slate-900 via-slate-800 to-slate-900">
      {/* Header */}
      <header className="border-b border-slate-700 bg-slate-800/80 backdrop-blur-sm sticky top-0 z-40">
        <div className="mx-auto max-w-6xl px-6 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-3">
              <div className="w-10 h-10 rounded-xl bg-linear-to-br from-emerald-500 to-blue-600 flex items-center justify-center">
                <span className="text-xl">üé∞</span>
              </div>
              <div>
                <h1 className="text-lg font-bold text-white">{agent?.name}</h1>
                <p className="text-xs text-slate-400">{agent?.email}</p>
              </div>
            </div>

            {/* Saldo do Pool */}
            <div className="flex items-center gap-2 bg-slate-700/50 rounded-xl px-6 py-3 border border-slate-600">
              <span className="text-2xl">üè¶</span>
              <div className="text-right">
                <p className="text-xs text-slate-400">Saldo do Pool</p>
                <p className={`text-2xl font-bold ${(pool?.balance || 0) > 0 ? 'text-emerald-400' : 'text-red-400'}`}>
                  {formatCurrency(pool?.balance || 0)}
                </p>
              </div>
            </div>

            <button
              onClick={handleLogout}
              className="flex items-center gap-2 rounded-lg bg-slate-700 hover:bg-red-600/20 hover:text-red-400 border border-slate-600 hover:border-red-500/50 px-4 py-2 text-sm text-slate-300 transition"
            >
              <span>üö™</span>
              Sair
            </button>
          </div>
        </div>
      </header>

      {/* Navigation */}
      <nav className="border-b border-slate-700/50 bg-slate-800/30">
        <div className="mx-auto max-w-6xl px-6">
          <div className="flex gap-1">
            <Link href="/dashboard" className="px-4 py-3 text-sm font-medium text-slate-400 hover:text-white border-b-2 border-transparent hover:border-slate-600 transition">
              üìä Dashboard
            </Link>
            <Link href="/dashboard/pool" className="px-4 py-3 text-sm font-medium text-emerald-400 border-b-2 border-emerald-400">
              üè¶ Pool
            </Link>
            <Link href="/dashboard/games" className="px-4 py-3 text-sm font-medium text-slate-400 hover:text-white border-b-2 border-transparent hover:border-slate-600 transition">
              üéÆ Jogos
            </Link>
            <Link href="/dashboard/transactions" className="px-4 py-3 text-sm font-medium text-slate-400 hover:text-white border-b-2 border-transparent hover:border-slate-600 transition">
              üìã Transa√ß√µes
            </Link>
            <Link href="/dashboard/integration" className="px-4 py-3 text-sm font-medium text-slate-400 hover:text-white border-b-2 border-transparent hover:border-slate-600 transition">
              üîó Integra√ß√£o
            </Link>
            <Link href="/dashboard/settings" className="px-4 py-3 text-sm font-medium text-slate-400 hover:text-white border-b-2 border-transparent hover:border-slate-600 transition">
              ‚öôÔ∏è Configura√ß√µes
            </Link>
          </div>
        </div>
      </nav>

      {/* Main Content */}
      <main className="mx-auto max-w-6xl px-6 py-8">
        {error && (
          <div className="mb-6 p-4 rounded-lg bg-red-500/20 border border-red-500/50 text-red-300">
            {error}
          </div>
        )}

        {/* Fase Atual + A√ß√µes R√°pidas */}
        <div className="grid gap-4 lg:grid-cols-3 mb-8">
          {/* Fase Atual */}
          <div className={`rounded-xl border p-6 ${getPhaseBgColor(pool?.currentPhase || 'normal')}`}>
            <div className="flex items-center justify-between mb-4">
              <h3 className="text-sm font-medium text-slate-300">Fase Atual</h3>
              <button
                onClick={() => setShowPhaseModal(true)}
                className="text-xs px-2 py-1 rounded bg-slate-700/50 hover:bg-slate-600/50 text-slate-300 transition"
              >
                Alterar
              </button>
            </div>
            <p className={`text-2xl font-bold ${getPhaseColor(pool?.currentPhase || 'normal')}`}>
              {getPhaseLabel(pool?.currentPhase || 'normal')}
            </p>
            <p className="text-sm text-slate-400 mt-2">
              {getPhaseDescription(pool?.currentPhase || 'normal')}
            </p>
            <div className="mt-4 pt-4 border-t border-slate-600/50">
              <div className="flex items-center gap-2 text-sm">
                <span className="text-slate-400">Auto-fase:</span>
                <span className={pool?.isAutoPhase ? 'text-emerald-400' : 'text-slate-500'}>
                  {pool?.isAutoPhase ? '‚úÖ Ativado' : '‚ùå Desativado'}
                </span>
              </div>
            </div>
          </div>

          {/* Limites Atuais */}
          <div className="rounded-xl border border-slate-700 bg-slate-800/50 p-6">
            <h3 className="text-sm font-medium text-slate-300 mb-4">Limites de Payout</h3>
            <div className="space-y-3">
              <div className="flex justify-between">
                <span className="text-slate-400">Max Payout:</span>
                <span className="text-white font-semibold">{formatCurrency(limits?.maxPayout || 0)}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-slate-400">Max Multiplicador:</span>
                <span className="text-white font-semibold">{limits?.maxMultiplier || 0}x</span>
              </div>
              <div className="flex justify-between">
                <span className="text-slate-400">Win Chance:</span>
                <span className="text-white font-semibold">{formatPercent(limits?.effectiveWinChance || 0)}</span>
              </div>
              <div className="flex justify-between items-center">
                <span className="text-slate-400">Status:</span>
                <span className={`px-2 py-1 rounded text-xs font-medium ${limits?.canPay ? 'bg-emerald-500/20 text-emerald-400' : 'bg-red-500/20 text-red-400'}`}>
                  {limits?.canPay ? '‚úÖ Pode Pagar' : '‚ùå Bloqueado'}
                </span>
              </div>
            </div>
          </div>

          {/* A√ß√µes R√°pidas */}
          <div className="rounded-xl border border-slate-700 bg-slate-800/50 p-6">
            <h3 className="text-sm font-medium text-slate-300 mb-4">A√ß√µes R√°pidas</h3>
            <div className="space-y-3">
              <button
                onClick={() => {
                  setModalAmount('');
                  setModalDescription('');
                  setModalError('');
                  setModalSuccess('');
                  setShowDepositModal(true);
                }}
                className="w-full flex items-center justify-center gap-2 px-4 py-3 rounded-lg bg-emerald-600 hover:bg-emerald-500 text-white font-medium transition"
              >
                <span>üì•</span> Depositar
              </button>
              <button
                onClick={() => {
                  setModalAmount('');
                  setModalDescription('');
                  setModalError('');
                  setModalSuccess('');
                  setShowWithdrawModal(true);
                }}
                className="w-full flex items-center justify-center gap-2 px-4 py-3 rounded-lg bg-slate-700 hover:bg-slate-600 text-white font-medium transition border border-slate-600"
              >
                <span>üì§</span> Sacar
              </button>
              <Link
                href="/dashboard/pool/transactions"
                className="w-full flex items-center justify-center gap-2 px-4 py-3 rounded-lg bg-slate-700/50 hover:bg-slate-600/50 text-slate-300 font-medium transition border border-slate-600/50"
              >
                <span>üìã</span> Ver Hist√≥rico
              </Link>
            </div>
          </div>
        </div>

        {/* Estat√≠sticas */}
        <div className="rounded-xl border border-slate-700 bg-slate-800/50 overflow-hidden mb-8">
          <div className="px-6 py-4 border-b border-slate-700 flex items-center justify-between">
            <h2 className="text-lg font-semibold text-white">üìà Estat√≠sticas</h2>
            <div className="flex gap-2">
              {(['24h', '7d', '30d', 'all'] as const).map((period) => (
                <button
                  key={period}
                  onClick={() => setStatsPeriod(period)}
                  className={`px-3 py-1 rounded text-sm font-medium transition ${
                    statsPeriod === period
                      ? 'bg-emerald-600 text-white'
                      : 'bg-slate-700 text-slate-300 hover:bg-slate-600'
                  }`}
                >
                  {period === '24h' ? '24h' : period === '7d' ? '7 dias' : period === '30d' ? '30 dias' : 'Tudo'}
                </button>
              ))}
            </div>
          </div>
          
          <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-4 p-6">
            <div className="rounded-lg bg-slate-700/30 p-4">
              <p className="text-sm text-slate-400">Total Apostado</p>
              <p className="text-xl font-bold text-white">{formatCurrency(stats?.totalBets || 0)}</p>
            </div>
            <div className="rounded-lg bg-slate-700/30 p-4">
              <p className="text-sm text-slate-400">Total Pago</p>
              <p className="text-xl font-bold text-white">{formatCurrency(stats?.totalPayouts || 0)}</p>
            </div>
            <div className="rounded-lg bg-slate-700/30 p-4">
              <p className="text-sm text-slate-400">Lucro L√≠quido</p>
              <p className={`text-xl font-bold ${(stats?.netProfit || 0) >= 0 ? 'text-emerald-400' : 'text-red-400'}`}>
                {formatCurrency(stats?.netProfit || 0)}
              </p>
            </div>
            <div className="rounded-lg bg-slate-700/30 p-4">
              <p className="text-sm text-slate-400">RTP</p>
              <p className="text-xl font-bold text-blue-400">{formatPercent(stats?.rtp || 0)}</p>
            </div>
          </div>

          <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-4 px-6 pb-6">
            <div className="rounded-lg bg-slate-700/30 p-4">
              <p className="text-sm text-slate-400">Total de Spins</p>
              <p className="text-xl font-bold text-white">{stats?.totalSpins?.toLocaleString('pt-BR') || 0}</p>
            </div>
            <div className="rounded-lg bg-slate-700/30 p-4">
              <p className="text-sm text-slate-400">Vit√≥rias</p>
              <p className="text-xl font-bold text-white">{stats?.totalWins?.toLocaleString('pt-BR') || 0}</p>
            </div>
            <div className="rounded-lg bg-slate-700/30 p-4">
              <p className="text-sm text-slate-400">Taxa de Vit√≥ria</p>
              <p className="text-xl font-bold text-yellow-400">{formatPercent(stats?.winRate || 0)}</p>
            </div>
            <div className="rounded-lg bg-slate-700/30 p-4">
              <p className="text-sm text-slate-400">Maior Ganho</p>
              <p className="text-xl font-bold text-emerald-400">{formatCurrency(stats?.biggestWin || 0)}</p>
            </div>
          </div>
        </div>

        {/* Configura√ß√£o de Fases */}
        <div className="rounded-xl border border-slate-700 bg-slate-800/50 overflow-hidden">
          <div className="px-6 py-4 border-b border-slate-700 flex items-center justify-between">
            <h2 className="text-lg font-semibold text-white">‚öôÔ∏è Configura√ß√£o de Fases</h2>
            <Link
              href="/dashboard/pool/settings"
              className="text-sm text-emerald-400 hover:text-emerald-300 transition"
            >
              Editar Configura√ß√µes ‚Üí
            </Link>
          </div>
          
          <div className="grid gap-4 sm:grid-cols-3 p-6">
            {/* Reten√ß√£o */}
            <div className={`rounded-lg p-4 border ${pool?.currentPhase === 'retention' ? 'border-red-500 bg-red-500/10' : 'border-slate-600 bg-slate-700/30'}`}>
              <div className="flex items-center gap-2 mb-3">
                <span className="text-xl">üîí</span>
                <h4 className="font-medium text-red-400">Reten√ß√£o</h4>
                {pool?.currentPhase === 'retention' && (
                  <span className="ml-auto text-xs bg-red-500/30 text-red-300 px-2 py-0.5 rounded">ATIVA</span>
                )}
              </div>
              <div className="space-y-2 text-sm">
                <div className="flex justify-between">
                  <span className="text-slate-400">Win Chance:</span>
                  <span className="text-white">{pool?.retentionWinChance || 10}%</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-slate-400">Max Mult:</span>
                  <span className="text-white">{pool?.retentionMaxMultiplier || 30}x</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-slate-400">Threshold:</span>
                  <span className="text-white">&lt; {formatCurrency(pool?.retentionThreshold || 1000)}</span>
                </div>
              </div>
            </div>

            {/* Normal */}
            <div className={`rounded-lg p-4 border ${pool?.currentPhase === 'normal' ? 'border-yellow-500 bg-yellow-500/10' : 'border-slate-600 bg-slate-700/30'}`}>
              <div className="flex items-center gap-2 mb-3">
                <span className="text-xl">‚öñÔ∏è</span>
                <h4 className="font-medium text-yellow-400">Normal</h4>
                {pool?.currentPhase === 'normal' && (
                  <span className="ml-auto text-xs bg-yellow-500/30 text-yellow-300 px-2 py-0.5 rounded">ATIVA</span>
                )}
              </div>
              <div className="space-y-2 text-sm">
                <div className="flex justify-between">
                  <span className="text-slate-400">Win Chance:</span>
                  <span className="text-white">{pool?.normalWinChance || 35}%</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-slate-400">Max Mult:</span>
                  <span className="text-white">{pool?.normalMaxMultiplier || 100}x</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-slate-400">Range:</span>
                  <span className="text-white">{formatCurrency(pool?.retentionThreshold || 1000)} - {formatCurrency(pool?.releaseThreshold || 10000)}</span>
                </div>
              </div>
            </div>

            {/* Libera√ß√£o */}
            <div className={`rounded-lg p-4 border ${pool?.currentPhase === 'release' ? 'border-emerald-500 bg-emerald-500/10' : 'border-slate-600 bg-slate-700/30'}`}>
              <div className="flex items-center gap-2 mb-3">
                <span className="text-xl">üí∞</span>
                <h4 className="font-medium text-emerald-400">Libera√ß√£o</h4>
                {pool?.currentPhase === 'release' && (
                  <span className="ml-auto text-xs bg-emerald-500/30 text-emerald-300 px-2 py-0.5 rounded">ATIVA</span>
                )}
              </div>
              <div className="space-y-2 text-sm">
                <div className="flex justify-between">
                  <span className="text-slate-400">Win Chance:</span>
                  <span className="text-white">{pool?.releaseWinChance || 65}%</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-slate-400">Max Mult:</span>
                  <span className="text-white">{pool?.releaseMaxMultiplier || 250}x</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-slate-400">Threshold:</span>
                  <span className="text-white">&gt; {formatCurrency(pool?.releaseThreshold || 10000)}</span>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Info Card */}
        <div className="mt-6 rounded-xl border border-blue-500/30 bg-blue-500/10 p-5">
          <div className="flex items-start gap-3">
            <span className="text-2xl">üí°</span>
            <div>
              <h3 className="font-medium text-blue-300">Como funciona o Pool?</h3>
              <p className="text-sm text-blue-200/70 mt-1">
                O pool √© a &quot;banca&quot; do seu cassino. Quando jogadores apostam, o valor entra no pool. 
                Quando ganham, o pr√™mio sai do pool. O sistema ajusta automaticamente a chance de vit√≥ria 
                baseado no saldo do pool para garantir sustentabilidade. Voc√™ pode for√ßar uma fase manualmente 
                ou deixar no modo autom√°tico.
              </p>
            </div>
          </div>
        </div>
      </main>

      {/* Modal de Dep√≥sito */}
      {showDepositModal && (
        <div className="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center z-50">
          <div className="bg-slate-800 rounded-xl border border-slate-700 p-6 w-full max-w-md mx-4">
            <h3 className="text-lg font-semibold text-white mb-4">üì• Depositar no Pool</h3>
            
            {modalError && (
              <div className="mb-4 p-3 rounded-lg bg-red-500/20 border border-red-500/50 text-red-300 text-sm">
                {modalError}
              </div>
            )}
            {modalSuccess && (
              <div className="mb-4 p-3 rounded-lg bg-emerald-500/20 border border-emerald-500/50 text-emerald-300 text-sm">
                {modalSuccess}
              </div>
            )}

            <div className="space-y-4">
              <div>
                <label className="block text-sm text-slate-400 mb-1">Valor (R$)</label>
                <input
                  type="number"
                  value={modalAmount}
                  onChange={(e) => setModalAmount(e.target.value)}
                  placeholder="0.00"
                  className="w-full px-4 py-3 rounded-lg bg-slate-700 border border-slate-600 text-white placeholder-slate-400 focus:outline-none focus:border-emerald-500"
                />
              </div>
              <div>
                <label className="block text-sm text-slate-400 mb-1">Descri√ß√£o (opcional)</label>
                <input
                  type="text"
                  value={modalDescription}
                  onChange={(e) => setModalDescription(e.target.value)}
                  placeholder="Ex: Aporte inicial"
                  className="w-full px-4 py-3 rounded-lg bg-slate-700 border border-slate-600 text-white placeholder-slate-400 focus:outline-none focus:border-emerald-500"
                />
              </div>
            </div>

            <div className="flex gap-3 mt-6">
              <button
                onClick={() => setShowDepositModal(false)}
                className="flex-1 px-4 py-3 rounded-lg bg-slate-700 hover:bg-slate-600 text-white font-medium transition"
              >
                Cancelar
              </button>
              <button
                onClick={handleDeposit}
                disabled={modalLoading}
                className="flex-1 px-4 py-3 rounded-lg bg-emerald-600 hover:bg-emerald-500 text-white font-medium transition disabled:opacity-50"
              >
                {modalLoading ? 'Processando...' : 'Confirmar'}
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Modal de Saque */}
      {showWithdrawModal && (
        <div className="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center z-50">
          <div className="bg-slate-800 rounded-xl border border-slate-700 p-6 w-full max-w-md mx-4">
            <h3 className="text-lg font-semibold text-white mb-4">üì§ Sacar do Pool</h3>
            
            <div className="mb-4 p-3 rounded-lg bg-slate-700/50 border border-slate-600">
              <p className="text-sm text-slate-400">Saldo dispon√≠vel:</p>
              <p className="text-lg font-bold text-emerald-400">{formatCurrency(pool?.balance || 0)}</p>
            </div>

            {modalError && (
              <div className="mb-4 p-3 rounded-lg bg-red-500/20 border border-red-500/50 text-red-300 text-sm">
                {modalError}
              </div>
            )}
            {modalSuccess && (
              <div className="mb-4 p-3 rounded-lg bg-emerald-500/20 border border-emerald-500/50 text-emerald-300 text-sm">
                {modalSuccess}
              </div>
            )}

            <div className="space-y-4">
              <div>
                <label className="block text-sm text-slate-400 mb-1">Valor (R$)</label>
                <input
                  type="number"
                  value={modalAmount}
                  onChange={(e) => setModalAmount(e.target.value)}
                  placeholder="0.00"
                  max={pool?.balance || 0}
                  className="w-full px-4 py-3 rounded-lg bg-slate-700 border border-slate-600 text-white placeholder-slate-400 focus:outline-none focus:border-emerald-500"
                />
              </div>
              <div>
                <label className="block text-sm text-slate-400 mb-1">Descri√ß√£o (opcional)</label>
                <input
                  type="text"
                  value={modalDescription}
                  onChange={(e) => setModalDescription(e.target.value)}
                  placeholder="Ex: Retirada de lucros"
                  className="w-full px-4 py-3 rounded-lg bg-slate-700 border border-slate-600 text-white placeholder-slate-400 focus:outline-none focus:border-emerald-500"
                />
              </div>
            </div>

            <div className="flex gap-3 mt-6">
              <button
                onClick={() => setShowWithdrawModal(false)}
                className="flex-1 px-4 py-3 rounded-lg bg-slate-700 hover:bg-slate-600 text-white font-medium transition"
              >
                Cancelar
              </button>
              <button
                onClick={handleWithdraw}
                disabled={modalLoading}
                className="flex-1 px-4 py-3 rounded-lg bg-red-600 hover:bg-red-500 text-white font-medium transition disabled:opacity-50"
              >
                {modalLoading ? 'Processando...' : 'Confirmar Saque'}
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Modal de Alterar Fase */}
      {showPhaseModal && (
        <div className="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center z-50">
          <div className="bg-slate-800 rounded-xl border border-slate-700 p-6 w-full max-w-md mx-4">
            <h3 className="text-lg font-semibold text-white mb-4">üîÑ Alterar Fase do Pool</h3>
            
            <p className="text-sm text-slate-400 mb-4">
              Selecione a fase para for√ßar manualmente. Isso desativar√° o modo autom√°tico.
            </p>

            {modalError && (
              <div className="mb-4 p-3 rounded-lg bg-red-500/20 border border-red-500/50 text-red-300 text-sm">
                {modalError}
              </div>
            )}

            <div className="space-y-3">
              <button
                onClick={() => handleSetPhase('retention')}
                disabled={modalLoading}
                className={`w-full flex items-center gap-3 p-4 rounded-lg border transition ${
                  pool?.currentPhase === 'retention' 
                    ? 'border-red-500 bg-red-500/20' 
                    : 'border-slate-600 bg-slate-700/50 hover:bg-slate-700'
                }`}
              >
                <span className="text-2xl">üîí</span>
                <div className="text-left">
                  <p className="font-medium text-red-400">Reten√ß√£o</p>
                  <p className="text-xs text-slate-400">Menos vit√≥rias, acumula saldo</p>
                </div>
                {pool?.currentPhase === 'retention' && (
                  <span className="ml-auto text-xs bg-red-500/30 text-red-300 px-2 py-0.5 rounded">ATUAL</span>
                )}
              </button>

              <button
                onClick={() => handleSetPhase('normal')}
                disabled={modalLoading}
                className={`w-full flex items-center gap-3 p-4 rounded-lg border transition ${
                  pool?.currentPhase === 'normal' 
                    ? 'border-yellow-500 bg-yellow-500/20' 
                    : 'border-slate-600 bg-slate-700/50 hover:bg-slate-700'
                }`}
              >
                <span className="text-2xl">‚öñÔ∏è</span>
                <div className="text-left">
                  <p className="font-medium text-yellow-400">Normal</p>
                  <p className="text-xs text-slate-400">Equil√≠brio entre ganhos e reten√ß√£o</p>
                </div>
                {pool?.currentPhase === 'normal' && (
                  <span className="ml-auto text-xs bg-yellow-500/30 text-yellow-300 px-2 py-0.5 rounded">ATUAL</span>
                )}
              </button>

              <button
                onClick={() => handleSetPhase('release')}
                disabled={modalLoading}
                className={`w-full flex items-center gap-3 p-4 rounded-lg border transition ${
                  pool?.currentPhase === 'release' 
                    ? 'border-emerald-500 bg-emerald-500/20' 
                    : 'border-slate-600 bg-slate-700/50 hover:bg-slate-700'
                }`}
              >
                <span className="text-2xl">üí∞</span>
                <div className="text-left">
                  <p className="font-medium text-emerald-400">Libera√ß√£o</p>
                  <p className="text-xs text-slate-400">Mais vit√≥rias, distribui lucros</p>
                </div>
                {pool?.currentPhase === 'release' && (
                  <span className="ml-auto text-xs bg-emerald-500/30 text-emerald-300 px-2 py-0.5 rounded">ATUAL</span>
                )}
              </button>
            </div>

            <button
              onClick={() => setShowPhaseModal(false)}
              className="w-full mt-4 px-4 py-3 rounded-lg bg-slate-700 hover:bg-slate-600 text-white font-medium transition"
            >
              Cancelar
            </button>
          </div>
        </div>
      )}
    </div>
  );
}
